import * as axios from 'axios'

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: { 'API-KEY': '048e4b90-f2e6-403b-aecd-c308f4d2756f' }
})

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 5) {
        console.log('getUsers have been done')
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(response => { return response.data })
    },
    updateUsers(pageNumber, pageSize) {
        console.log('getUsers have been done')
        return instance.get(`users?page=${pageNumber}&count=${pageSize}`)
            .then(response => { return response.data })
    },

    followOnUser(UserId = 2) {
        console.log('You have been followed on User with id: ' + UserId)
        return instance.post(`follow/${UserId}`)
            .then(response => { return response.data })
    },

    unfollowOnUser(UserId = 2) {
        console.log('You have been unfollowed on User with id: ' + UserId)
        return instance.delete(`follow/${UserId}`)
            .then(response => { return response.data })
    }
}

export const profileAPI = {
    getProfile(UserId) {
        return instance.get(`profile/${UserId}`).then(response => { return response.data })
    },
    getStatus(UserId) {
        return instance.get(`profile/status/${UserId}`).then(response => { return response })
    },
    updateStatus(status) {
        return instance.put(`profile/status`, { status }).then(response => { return response })
    }
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`)
            .then(response => { return response.data })
    },

    login(email, password, rememberMe = false) {
        return instance.post('auth/login', { email, password, rememberMe })
    },

    logout() {
        return instance.delete('auth/login')
    }
}